import React from 'react';
import PropTypes from 'prop-types';
import { useKeycloak } from 'react-keycloak';
import { Button } from 'carbon-components-react';

import './LoginButtonWidget.scss';

const ButtonKeycloakLogic = ({ applicationBaseURL, sessionUser, userDisplayName, currentPage }) => {
  const [keycloak] = useKeycloak();

  const handleLogin = () => {
    window.location.href = `${applicationBaseURL}do/login?redirectTo=${currentPage}`;
  };

  return (
    <div className="LoginButtonWidget">
      {keycloak.authenticated ? (
        <div className="LoginButtonWidget__wellcome">
          Welcome, {userDisplayName}! (<a href={`${applicationBaseURL}do/logout.action`}>Logout</a>)
        </div>
      ) : (
        <Button onClick={handleLogin}>Login</Button>
      )}
    </div>
  )

}

ButtonKeycloakLogic.propTypes = {
  applicationBaseURL: PropTypes.string.isRequired,
  sessionUser: PropTypes.string,
  userDisplayName: PropTypes.string,
  currentPage: PropTypes.string,
};

ButtonKeycloakLogic.defaultProps = {
  sessionUser: '',
  userDisplayName: '',
  currentPage: '',
};


export default ButtonKeycloakLogic;
